.article-page {
  &__header {
    margin-bottom: 40px;
  }

  &__title {
    margin-bottom: 30px;
    font-weight: 700;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__meta {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__meta-item {
        font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: rgba(54, 54, 54, 0.7);

    @include mobile {
      font-size: 13px;
    }
  }
}

