.block-description {
  &__title {
    margin-bottom: 40px;

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    gap: 40px;

    @include tablet {
      gap: 20px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__img {
    @include mobile {
      height: 300px;
    }

    img {
      border-radius: 30px;

      @include mobile {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__descr {
    width: calc(100% - 590px);

    @include desktop {
      width: calc(100% - 414px);
    }

    @include tablet {
      width: calc(100% - 170px);
    }

    @include mobile {
      width: 100%;
    }
  }
}
