.sort {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 40px;
  gap: 20px;

  @include small-tablet {
    margin-bottom: 30px;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: #363636;

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      display: none;
    }
  }

  &__btn {
    position: relative;
    font-weight: 400;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: -0.01em;
    color: #363636;

    @include small-tablet {
      font-size: 16px;
    }

    &::after {
      content: '';

      /* создаем псевдоэлемент */
      position: absolute;

      /* позиционируем относительно ссылки */
      left: 0;

      /* выравнивание по левому краю */
      bottom: -2px;

      /* отступ от текста */
      display: block;

      /* делаем его блочным элементом */
      width: 100%;

      /* ширина линии */
      height: 4px;

      /* высота линии */
      background: radial-gradient(circle, #363636 30%, transparent 30%) center;

      /* точечный фон */
      background-size: 10px 4px;

      /* размер точек */
    }
  }
}
