.article-page__container {
  display: flex;
  align-items: flex-start;
  gap: 40px;

  @include tablet {
    flex-direction: column;
  }
}

.article-page__sidebar {
  position: sticky;
  top: 20px;
  flex-shrink: 0;
  width: 320px;

  @include desktop {
    width: 250px;
  }

  @include tablet {
    display: none;
  }

}

.article-page__inner {
  width: 100%;
  min-width: 0;

}
