.social {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;

  &__text {
    font-weight: 400;
    font-size: 15px;
    line-height: 105%;
    color: #fff;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__item {
    width: 40px;
    height: 40px;
  }

  &__link {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__link--tg {
    background-image: url("../img/tg.svg");
  }

  &__link--vk {
    background-image: url("../img/vk.svg");
  }

  &__link--yt {
    background-image: url("../img/yt.svg");
  }

  &__link--rut {
    background-image: url("../img/rut.png");
  }

  &__link--dz {
    background-image: url("../img/dz.svg");
  }

  &__link--wa {
    background-image: url("../img/wa.svg");
  }
}

.market-social {
  text-align: center;

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;

    @include mobile {
      gap: 10px;
    }
  }

  &__item {
    margin-bottom: 5px;
    width: 100%;
    height: 35px;
  }

  &__link {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &__link--tg {
    width: 35px;
    height: 35px;
    background-image: url("../img/tg-market.svg");
  }

  &__link--wa {
    width: 35px;
    height: 35px;
    background-image: url("../img/wa-market.svg");
  }

  &__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: -0.07em;
    text-align: center;
    color: rgba(54, 54, 54, 0.7);
  }

  &__link--ozon {
    width: 80px;
    height: 35px;
    background-image: url("../img/ozon-market.svg");
  }

  &__link--wb {
    width: 50px;
    height: 35px;
    background-image: url("../img/wb-market.svg");
  }
}
