.article__gallery {
  position: relative;
}

.gallery-slider {
  position: relative;
  width: 100%;
  max-width: 994px;

  a {
    display: inline-block;
    width: 100%;
    height: 631px;

    @include tablet {
      height: 467px;
    }

    @include mobile {
      height: 370px;
    }
  }

  img {
    border-radius: 30px;
    width: 100%;
    height: 100%;
  }

  &__btns {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    margin-left: auto;
    width: 200px;

    @include mobile {
      position: relative;
      margin-right: auto;
    }
  }

  &__btn {
    z-index: 11;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    background-color: #fbfff5;

    svg {
      width: 16px;
      height: 28px;
      fill: transparent;
      stroke: #363636;
      stroke-opacity: 0.7;
      stroke-width: 2px;
    }
  }

  &__prev {
    &.swiper-button-disabled {
      background-color: transparent;
    }
  }

  &__next {
     &.swiper-button-disabled {
      background-color: transparent;
    }
  }


  &__pag {
    left: 50%;
    top: 50%;
    bottom: inherit;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: rgba(54, 54, 54, 0.7);
    translate: -50% -50%;

    .swiper-pagination-current {
      padding-right: 10px;
    }

    .swiper-pagination-total {
      padding-left: 10px;
    }
  }
}
