.note {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #0d593b;
  border-radius: 20px;
  padding: 25px;
  gap: 30px;

  &__warning {
    flex-shrink: 0;
    border-radius: 50%;
    width: 48px;
    height: 48px;
    font-weight: 700;
    font-size: 30px;
    line-height: 1.5;
    text-align: center;
    color: #fff;
    background-color: #0D593B;
  }

  &__text {
    max-width: 650px;
    font-weight: 400;
    font-size: 24px;
    line-height: 140%;
    color: #363636;

    @include small-tablet {
      font-size: 18px;
    }

    @include mobile {
      font-size: 16px;
    }
  }
}
