.article {
  display: flex;
  flex-direction: column;
  border-radius: 30px;
  padding: 20px;
  height: 100%;
  background-color: #fbfff5;
  gap: 20px;

  @include desktop {
    padding: 10px;
    gap: 10px;
  }

  &__descr {
    flex-grow: 1;
  }

  &__date {
    display: inline-block;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 110%;
    color: #363636;

    @include desktop {
      margin-bottom: 10px;
    }
  }

  &__item-title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 22px;
    line-height: 110%;
    color: #0d593b;

    @include desktop {
      font-size: 20px;
    }

     @include mobile {
      font-size: 18px;
    }
  }

  &__item-text {
    display: -webkit-box; overflow: hidden;
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    color: #0d593b; -webkit-box-orient: vertical; -webkit-line-clamp: 3;
  }

  &__item-img {
    margin-top: auto;
    height: 325px;

    @include small-tablet {
      height: 250px;
    }

    img {
      border-radius: 20px;
      width: 100%;
      height: 100%;
    }
  }
}


