.video {
  position: relative;
  display: inline-block;
  overflow: hidden;
  border-radius: 40px;
  width: 100%;
  height: 600px;
  background-color: #333;

  @include tablet {
    height: 500px;
  }

  @include mobile {
    height: 370px;
  }

  &__thumbnail {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;

  }

  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    border: none;
    padding: 0;
    width: 170px;
    height: 170px;
    background-color: transparent;
    cursor: pointer;
    translate: -50% -50%;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  iframe,
  video {
    display: block;
    border: none;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
