.footer {
  padding: 20px 0;
  background-color: #0d593b;

  .container {
    @include desktop {
      max-width: 100%;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 49px;

    @include desktop {
      gap: 30px;
    }

    @include tablet {
      flex-wrap: wrap;
      gap: 20px;
    }
  }

  &__logo {
    flex-shrink: 0;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 49px;

    @include desktop {
      gap: 20px;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__descr {
    display: inline-block;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #fff;

    @include small-tablet {
      font-size: 13px;
    }

    @include mobile {
      font-size: 15px;
    }
  }
}
