.header {
  position: fixed;
  z-index: 100;
  padding: 15px;
  width: 100%;
  background-color: lightblue;

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 32px;
  }

  &__logo {
    flex-shrink: 0;
    width: 170px;
    height: 20px;

    use {
      fill: #0D593B;
    }
  }

  &__page-list {
    display: flex;
    align-items: center;
    border: 1px solid #fff;
    border-radius: 100px;
    padding: 5px;
    gap: 20px;
  }

  &__page-item {
    border-radius: 500px;
    padding: 10px 20px;
    color: #0d593b;
  }

  &__page-item--active {
    color: #fff;
    background-color: #0d593b;
  }

  &__page-link {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    color: inherit;
  }

  &__social {}
}

.nav {
  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
  }

  &__item {}

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    color: #0d593b;
  }
}

