.related {
  ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    list-style: none;
    margin: 0;
    padding: 0;
    gap: 30px;

    @include small-tablet {
      gap: 20px;
    }

    @include mobile {
      grid-template-columns: repeat(1, 1fr);

    }
  }

  li {
    position: relative;
    display: flex;
    align-items: center;
    gap: 15px;
  }

  a {
    position: absolute;
    inset: 0;
    align-self: center;
    padding-left: calc(168px + 15px);
    font-weight: 400;
    font-size: 24px;
    line-height: 130%;
    color: #363636;

    @include desktop {
      font-size: 20px;
    }

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  img {
    border-radius: 8px;
  }
}
