*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
  background-color: #f8f8f8;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  // overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}


.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width-xl);

  @include desktop {
    max-width: var(--container-width);
  }
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title {
  margin: 0;
  font-weight: 500;
  font-size: 40px;
  line-height: 140%;
  color: #0d593b;

  @include tablet {
    font-size: 30px;
  }
}

.section-padding {
  padding-bottom: 60px;

  @include mobile {
    padding-bottom: 40px;
  }
}

.section-text {
  h2 {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 30px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #0d593b;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  h3 {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 24px;
    line-height: 140%;
    color: #0d593b;

    @include desktop {
      font-size: 22px;
    }
  }

  p {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: #0d593b;

    @include desktop {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 16px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  ul {
    margin: 0;
    margin-bottom: 20px;
    padding: 0;
    padding-left: 26px;
  }

  li {
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: #0d593b;

    @include desktop {
      font-size: 18px;
    }

    @include small-tablet {
      font-size: 16px;
    }

    &:not(:last-child) {
      margin-bottom: 16px;

      @include small-tablet {
        margin-bottom: 10px;
      }
    }
  }
}

.section-table {
  table {
    margin-bottom: 30px;
    border-collapse: collapse;
    width: 100%;

    th,
    td {
      border-bottom: 1px solid #0d593b;
      padding: 18px 0 20px;
      text-align: center;

      @include mobile {
        padding: 10px 0;
      }
    }

    thead th {
      border-bottom: none;
      font-weight: 700;
      font-size: 20px;
      line-height: 140%;
      text-align: center;
      color: #0d593b;

      @include mobile {
        font-size: 14px;
      }
    }

    tbody tr {
      font-weight: 400;
      font-size: 17px;
      line-height: 140%;
      color: #363636;

      @include mobile {
        font-size: 13px;
      }
    }

    tbody tr td:first-child {
      width: 45%;
      text-align: left;
    }

    tbody tr td:nth-child(2),
    tbody tr td:nth-child(3) {
      width: 27.5%;
    }
  }
}

.section-pb {
  padding-bottom: 70px;

  @include desktop {
    padding-bottom: 40px;
  }
}
