.toc {
  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #0d593b;
  }

  &__list {
    li {
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: rgba(54, 54, 54, 0.7);

      &:not(:last-child) {
        margin-bottom: 8px;
      }
    }

    a {
      color: inherit;
    }
  }

  .toc--active {
    color: #363636;
  }
}
