.tags {
  display: flex;
  flex-wrap: wrap;
  justify-content: start;
  gap: 20px;

  @include small-tablet {
    gap: 10px;
  }

  &__link {
    display: inline-block;
    border-radius: 50px;
    padding: 10px 20px;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    color: #0d593b;
    background-color: #fff;
    transition: 0.3s ease-in background-color, 0.3s ease-in color;

    @include desktop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 16px;
    }

    @include small-tablet {
      padding: 10px 16px;
    }

    &:hover {
      color: #fff;
      background-color: #0d593b;
    }
  }
}
