.search-icon {
  position: relative;
  display: inline-block;

  @include small-tablet {
    margin-right: 25px;
  }

  @include mobile {
    margin-right: 10px;
  }
}

.search-icon__btn {
  display: inline-flex;
  align-items: center;
  border: none;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  color: var(--dark-green);
  background: none;
  cursor: pointer;
}

.search-icon__svg {
  width: 40px;
  height: 40px;
}

.search-icon__tooltip {
  position: absolute;
  left: -45px;
  top: 50%;
  display: none;
  width: 200px;
  transform: translateY(-50%);

  @include small-tablet {
    left: 0;
    top: 100%;
    bottom: 0;
    z-index: 10;
    width: 235px;
  }
}

.search-icon__input {
  border: 2px solid var(--dark-green);
  border-radius: 10px;
  padding: 12px 30px 12px 12px;
  width: 100%;
  font-weight: 700;
  font-size: 20px;
  line-height: 120%;
  color: var(--dark-green);
}

.search-icon__close {
  position: absolute;
  right: 5px;
  top: 50%;
  width: 24px;
  height: 24px;
  background-image: url("../img/close.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transform: translateY(-50%);
  cursor: pointer;

  @include small-tablet {
    top: 12px;
    transform: translateY(0);
  }
}
