.hero {
  position: relative;
  margin-bottom: 80px;

  @include tablet {
    margin-bottom: 60px;
  }

  @include mobile {
    margin-bottom: 40px;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    z-index: -1;
    display: block;
    width: 46%;
    height: 100%;
    background-color: #0d593b;

    @include tablet {
      width: 53.5%;
    }

    @include mobile {
      display: none;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 80px;

    @include mobile {
      flex-direction: column;
      gap: 10px;
    }
  }

  &__block-small {
    width: 41%;

    @include tablet {
      padding: 70px 0;
      width: 50%;
    }

    @include mobile {
      padding: 0;
      width: 100%;
    }
  }

  &__block {
    width: 50%;

    @include mobile {
      width: 100%;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 90px;
    line-height: 110%;
    color: #fff;

    @include desktop {
      margin-bottom: 30px;
      font-size: 70px;
    }

    @include tablet {
      margin-bottom: 20px;
      font-size: 50px;
    }

    @include mobile {
      font-size: 35px;
      color: #0d593b;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 110%;
    color: #fff;

    @include tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 15px;
      color: #0d593b;
    }
  }

  &__img {
    padding-top: 30px;
    max-width: 658px;

    @include mobile {
      padding-top: 0;
      height: 300px;
      max-width: 100%;
      text-align: center;
    }

    img {
      @include mobile {
        height: 100%;
      }
    }
  }
}
