.market {
  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 700;
    font-size: 60px;
    line-height: 110%;
    color: #0d593b;

    @include tablet {
      font-size: 40px;
    }

    @include mobile {
      font-size: 30px;
    }
  }

  &__tags {
    margin-bottom: 40px;
  }

  &__row {
    margin-bottom: 40px;
  }

  &__block {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
    gap: 10px;

    @include tablet {
      justify-content: flex-start;
      gap: 10px 40px;
    }

    @include mobile {
      justify-content: center;
      gap: 10px;
    }
  }

  &__descr {
    display: flex;
    align-items: center;
    gap: 50px;

    @include tablet {
      width: 100%;
    }
  }

  &__block-title {
    margin: 0;
    font-weight: 500;
    font-size: 40px;
    line-height: 110%;
    color: #0d593b;

    @include tablet {
      font-size: 24px;
    }
  }

  &__block-size {
    position: relative;
    font-weight: 400;
    font-size: 24px;
    line-height: 100%;
    letter-spacing: -0.07em;
    text-align: center;
    color: #0d593b;

    @include tablet {
      font-size: 20px;
    }

    &::before {
      content: "";
      position: absolute;
      left: -25px;
      top: 50%;
      width: 22px;
      height: 30px;
      background-image: url("../img/size-icon.png");
      background-repeat: no-repeat;
      transform: translateY(-50%);
    }
  }

  &__price {
    display: flex;
    align-items: center;
    gap: 15px;

  }

  &__price-text {
    font-weight: 400;
    font-size: 24px;
    line-height: 100%;
    letter-spacing: -0.07em;
    text-align: center;
    color: #0d593b;

    @include tablet {
      font-size: 20px;
    }
  }

  &__price-sum {
    font-weight: 500;
    font-size: 30px;
    line-height: 100%;
    letter-spacing: -0.07em;
    text-align: center;
    color: #0d593b;

    @include tablet {
      font-size: 24px;
    }
  }

  &__social {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;

    @include mobile {
      gap: 15px;
    }
  }

  &__social-text {
    font-weight: 500;
    font-size: 28px;
    line-height: 100%;
    letter-spacing: -0.07em;
    text-align: center;
    color: #0d593b;

    @include tablet {
      font-size: 24px;
    }

    @include mobile {
      font-size: 15px;
    }
  }

  &__comments {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;

    @include mobile {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__comments-text {
    font-weight: 400;
    font-size: 20px;
    line-height: 110%;
    color: #363636;

    @include tablet {
      font-size: 15px;
    }
  }

  &__comments-text--light {
    color: rgba(54, 54, 54, 0.7);
  }
}

.market-slider {
  margin-bottom: 25px;
  height: 400px;

  @include desktop {
    height: 325px;
  }

  @include small-tablet {
    height: 250px;
  }

  &__preview {
    display: block;
    width: 100%;
    height: 100%;

    img {
      border-radius: 20px;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }


  &__play {
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 10;
    border: none;
    padding: 0;
    width: 100px;
    height: 100px;
    background-color: transparent;
    cursor: pointer;
    translate: -50% -50%;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.slide-video {
  @include mobile {
    margin-bottom: 20px;
    width: 100% !important;
    height: 200px;
  }
}
