.posts {
  &__title {
    margin-bottom: 40px;

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__tags {
    margin-bottom: 40px;

    @include desktop {
      margin-bottom: 30px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 20px;

    @include tablet {
      grid-template-columns: repeat(6, 1fr);
    }
  }

  &__item {
    grid-column: 3 span;

    @include desktop {
      grid-column: 4 span;
    }

    @include tablet {
      grid-column: 2 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }
}
