.varieties {
  &__block {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }

  &__title {
    margin-bottom: 20px;

    @include mobile {
      margin-bottom: 15px;
    }
  }

  ul {
    column-count: 4;
    column-gap: 20px;
    margin: 0;
    padding: 0;
    padding-left: 26px;

    @include tablet {
    column-count: 3;
    }

    @include mobile {
      column-count: 1;
    }
  }

  li {
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
    color: #0d593b;

    &:not(:last-child) {
      margin-bottom: 16px;

      @include small-tablet {
        margin-bottom: 12px;
      }
    }

    @include tablet {
      font-size: 16px;
    }
  }
}
